<script lang="ts">
export default {
  name: 'LoginPage',
}
</script>

<script setup lang="ts">
import { ref } from 'vue'
import { useRouter } from 'vue-router'
import { useUserStore } from '@/stores/userStore'
import { ROUTER_PATH } from '@/constants/menu'
import { SURGERY_ROOMS } from '@/constants/surgery'
import { ElMessage } from 'element-plus'

const router = useRouter()
const userStore = useUserStore()

// 登录表单数据
const loginForm = ref({
  operatingRoom: SURGERY_ROOMS[0].value || '', // 手术间
  employeeId: '', // 工号
  password: '', // 密码
})

// 手术间选项
const operatingRooms = ref(SURGERY_ROOMS)

const handleLogin = async () => {
  try {
    const { success, errorMsg } = await userStore.login(
      loginForm.value.employeeId,
      loginForm.value.password,
      loginForm.value.operatingRoom,
    )

    if (success) {
      ElMessage.success('登录成功')
      router.push(ROUTER_PATH.welcome)
    } else {
      ElMessage.error(errorMsg || '登录失败')
    }
  } catch (error) {
    console.error('登录失败:', error)
    ElMessage.error('登录失败，请稍后重试')
  }
}
</script>

<template>
  <div class="login-page">
    <!-- 左侧图片区域 -->
    <div class="login-left">
      <img src="@/assets/login.jpg" alt="登录插图" class="login-illustration" />
    </div>

    <!-- 右侧登录区域 -->
    <div class="login-right">
      <div class="login-container">
        <div class="login-header">
          <h2 class="login-title">登录</h2>
        </div>

        <el-form :model="loginForm" class="login-form">
          <el-form-item>
            <el-select
              v-model="loginForm.operatingRoom"
              placeholder="请选择手术间"
              class="login-input"
            >
              <el-option
                v-for="room in operatingRooms"
                :key="room.value"
                :label="room.label"
                :value="room.value"
              />
            </el-select>
          </el-form-item>

          <el-form-item>
            <el-input v-model="loginForm.employeeId" placeholder="用户工号" class="login-input" />
          </el-form-item>

          <el-form-item>
            <el-input
              v-model="loginForm.password"
              type="password"
              placeholder="密码"
              class="login-input"
            />
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="handleLogin" class="login-button"> 登录 </el-button>
          </el-form-item>
        </el-form>
      </div>

      <!-- 备案信息 -->
      <div class="login-footer">
        <p>备案号：浙ICP备xxxxxxx号-x</p>
      </div>
    </div>
  </div>
</template>

